import { Component, EventEmitter, Input, OnChanges, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { BsModalRef, ModalDirective } from 'ngx-bootstrap/modal';
import { FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry } from 'ngx-file-drop';
import { NgxImageCompressService } from 'ngx-image-compress';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { WIZARD_CONFIG } from '../../wizard.config';
import { WizardService } from '../../wizard.service';
import { OutputFormat } from 'ngx-image-cropper/lib/interfaces/cropper-options.interface';
import * as moment from 'moment-timezone';
export type IBSSCropConfig = {
  aspectRatio?:number;
  maintainAspectRatio?:boolean;
  format?: OutputFormat;
}

export interface IBSSFileConfig {
  formats?:any;
  sectionName:string;
  imageName:string;
  isUnique?:boolean;
  cropper:IBSSCropConfig;
  fileName?:string;
  customClass?:string;
}


@Component({
  selector: 'ibss-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.scss']
})
export class FileUploadComponent implements OnChanges {
  config: typeof CONFIG = CONFIG;
  wiz_config: typeof WIZARD_CONFIG = WIZARD_CONFIG;
  @Input() inputParam: any;
  @Input() fileConfig: IBSSFileConfig;
  @Input() existingFile: any;
  @Input() canRemoveImage: any = true;
  @Output() onSelectFile = new EventEmitter();
  @Output() onDeleteFile = new EventEmitter();
  @ViewChild(ModalDirective) cropImage: ModalDirective;
  showCrop = false;
  modalRef: BsModalRef;
  imageFile: any = '';
  croppedImage: any = '';
  files: NgxFileDropEntry[] = [];
  fileSize = 1024*1024;
  canCompress = false;
  cropper = {
    aspectRatio:1/1,
    maintainAspectRatio: true
  }
  modalConfig = {
    backdrop: true,
    ignoreBackdropClick: false,
    class: 'ibss-upload-components modal-lg'
  };
  allowedFileFormats = "";

  profile: any;
  isDefault = true;
  constructor(
    private toastr: ToastrService,
    private imageCompress: NgxImageCompressService,
    private ws: WizardService,
  ) {}

  ngOnChanges(): void {
    this.allowedFileFormats = this.fileConfig && this.fileConfig.formats && this.fileConfig.formats.length > 0 ? this.fileConfig.formats.join(',') : this.config.imageFormats.join(',');
    this.cropper = {...this.cropper, ...this.fileConfig.cropper}; 
    this.profile = this.ws.getProfileData();
    const currAssetsPath = this.config.assetsPath+this.profile.type+"/"+this.profile.title+"/";
    console.log("existingFile",this.existingFile);
    console.log("currAssetsPath",currAssetsPath);
    if (this.existingFile) {
      this.isDefault = this.existingFile.toLowerCase().indexOf(currAssetsPath.toLowerCase())==-1;
    }
    console.log("isDefault", this.isDefault);
  }

  public dropped(files: any) {
    this.files = files;
    for (const droppedFile of files) {
      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          // Here you can access the real file
          console.log(droppedFile.relativePath, file);

          this.canCompress = file.size > this.fileSize;
          const filename = file.name.split(".");
          const ext = filename[filename.length-1].toLowerCase();
          if (this.config.imageFormats.indexOf(ext) !==-1) {
            this.imageFile = file;
            this.onCropImage();
          } else {
            this.toastr.error("Invalid file format. Only png, gif and jpg formats are allowed");
          }
        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  onCompressImage(file:any) {
    let selectedFileSize = this.imageCompress.byteCount(file);
    console.warn('Selected File size :', selectedFileSize);
    if (this.canCompress) {
      this.imageCompress.getOrientation(file)
      .then ((orientation) => {
        console.log('orient', orientation)
          this.imageCompress.compressFile(file as string, orientation, 10, 10).then(
            (result) => {
              console.log(result);
              console.warn('Compressed File Size:', this.imageCompress.byteCount(result));
              // const compressedImg = base2Buff.decode(result);
              // console.log("compressedImg",compressedImg);
              this.saveFile(result);
            }, err => {
              console.log("error", err);
            });
      });
    } else {
      this.saveFile(file);
    }
  }

  imageCropped(event: any) {
    this.croppedImage = event.base64;
  }

  onCropImage() {
    // this.cropImage.show();
    this.showCrop = true;

  }

  saveCropped(): void {
    this.config.toggleLoader(true);
    this.onCompressImage(this.croppedImage);
    // this.cropImage.hide();
    this.showCrop = false;
  }
 
  cancelCrop(): void {
    // this.cropImage.hide();
    this.showCrop = false;
    this.imageFile = null;
    this.croppedImage = null;
  }

  saveFile(image:any) {

    const payload = {
      "serviceData": {
        "imageData": image.replace('data:image/png;base64,',''),
        "imageUrl": `${this.fileConfig.imageName+ (this.fileConfig.isUnique ? '_'+moment().unix():'')}.png`,
        "parentProfileName": "",
        "profileCategory": 1,
        "profileName": this.profile.title,
        "profileType": this.profile.type,
        "rootProfileName": "",
        "sectionName" : this.fileConfig.sectionName
      }
    }

    console.log("saveFile-payload", payload);
      // return;
    this.ws.saveFile(payload)
    .subscribe((res:any)=> {
        console.log("saveFile", res);
        this.config.toggleLoader(false);
        if (res.error.responseCode == 0) {
          this.onSelectFile.emit(res.serviceData.imageUrl);
          this.cancelCrop();
        } else {
          if (res.error.businessValidation.length > 0) {
            for (let i = 0; i < res.error.businessValidation.length; i++) {
              this.toastr.error(res.error.businessValidation[i].errorDesc);
            }
          }
          if (res.error.dataValidation.length > 0) {
            for (let i = 0; i < res.error.dataValidation.length; i++) {
              this.toastr.error(res.error.dataValidation[i].errorDesc);
            }
          }
        }
    },(err:any)=> {
      this.config.toggleLoader(false);
      this.toastr.error("Please try again after sometimes");
    })
  }

  onDelete() {
    this.onDeleteFile.emit(true);
  }
}
